import React from 'react'

function BigImg() {
    const refAssignCallback = (ref) => {
        if (ref) {
          //the containerRef is currently null, ref available = mounted.
          var element = ref;
          var scrollWidth = element.scrollWidth;
          var clientWidth = element.getBoundingClientRect().width;
    
          //explicitly set the scrollTop position of the scrollContainer
          element.scrollLeft = (scrollWidth - clientWidth) / 2.3;
        } 
      }; 
  return (
    <div className='h-screen w-full overflow-auto snap-center' ref={refAssignCallback}>
        <div className='h-screen w-[1550px]' style={{background: 'url("images/phone/13.png") no-repeat ',backgroundPosition: 'center ', backgroundSize: 'contain' }}></div>
    </div>
  )
}

export default BigImg