import React, { useEffect, useState } from 'react'
import Img from '../components/Img'
import PhoneImg from "../components/phone/PhoneImg";
import BigImg from "../components/phone/BigImg";
import FirstComponent from "../components/phone/FirstComponent";
import SecondComponent from "../components/phone/SecondComponent";
import ThirdComponent from "../components/phone/ThirdComponent";
import SendEmail from './SendEmail';
import $ from "jquery";
import { animateScroll as scroll, Element } from 'react-scroll';
import smoothscroll from 'smoothscroll-polyfill';
 
// kick off the polyfill!


function PhoneComponent() {
    const [first,setFirst] = useState(false)
    const [second,setSecond] = useState(false)
    const [third,setThird] = useState(false)
    const elementInView = (el) => {
      const elementTop = el.getBoundingClientRect().top;
      return elementTop 
    }
    const catchScrollHeightAndElementDistance = (elementid ) => {
      const element= document.querySelector(elementid)
      const scrollHeight = elementInView(element)
      return scrollHeight
    }
    const scrollElement = () => {
      
      const gapOnDifferentViewport = window.innerHeight - 873
      const scrollHeight = catchScrollHeightAndElementDistance("#firstcontainer")
      //alert(-440 + ((-gapOnDifferentViewport) * 2))
      if(scrollHeight <= (126 + (-gapOnDifferentViewport))){
        setFirst(true)
      }
      if(scrollHeight <= (-540 + ((-gapOnDifferentViewport) ))){
        setSecond(true)
      }
      if(scrollHeight <= (-2500 + ((-gapOnDifferentViewport) * 3))){
        setThird(true)
       
      }

    }

    const scrollToTop = (e) => {
      const mainContainer = document.querySelector('#mainContainer')
      mainContainer.scrollTo({ top: 0, behavior: 'smooth' })
      
    }

    useEffect(() => {
      const main = document.querySelector('#mainContainer')
      main.addEventListener('scroll',scrollElement)
      smoothscroll.polyfill()
      window.addEventListener('load', () => {
        setTimeout(() => {
          main.scrollTo(0,1)
        }, 0)
      })
    },[])

    return (
        <main className='overflow-x-hidden scroll-smooth ' style={{scrollSnapType: 'y mandatory',overflowY: 'scroll',height: '100vh'}} id='mainContainer'>
            
            <Img image={'images/phone/12.png'} />
           
            <FirstComponent first={first}/>
            <SecondComponent second={second}/>
            <BigImg />
            <PhoneImg />
            <ThirdComponent third={third}/>
            <SendEmail />
            <div className='w-14 h-14 fixed bottom-0 right-0 bg-yellow-500 text-white hover:bg-yellow-400 mb-10 flex justify-center items-center cursor-pointer' onClick={(e) => scrollToTop(e)}>
                <span className='font-black'>TOP</span>
            </div>

        </main>
    )
}

export default PhoneComponent