import React, {useEffect, useState} from 'react'

function SendEmail() {
  const [name,setName] = useState('')
  const [phone,setPhone] = useState('')
  const [email, setEmail] = useState('')
  const [area,setArea] = useState('')
  const [check,setCheck] = useState(false)
  const [mediaQuery,setMediaQuery] = useState(false)
  const [imgUrl,setImgUrl] = useState('')
  const confirmBtn = () => {
    if(name==='' || phone==='' || email==='' || area==='' || check === false){
       alert('')
       return
    }
    //setname null
    
   
    fetch("https://formsubmit.co/ajax/rachel7465022@gmail.com", {
        method: "POST",
        headers: { 
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        },
        body: JSON.stringify({
            '': name,
            '': email,
            '': phone,
            '': area
        })
    })
    .then(response => response.json())
    .then(data => {
        alert('')
        setName('')
        setPhone('')
        setEmail('')
        setArea('')
        setCheck(false)
    })
    .catch(error => console.log(error));
  }

  useEffect(() => {
      setMediaQuery(window.matchMedia('(max-width: 425px)').matches)
      window.addEventListener('resize',() => {
        setMediaQuery(window.matchMedia('(max-width: 425px)').matches)
      })
   },[])

   useEffect(() => {
    if(mediaQuery){
        setImgUrl('images/phone/6.png')
    }else{
        setImgUrl('images/PC/8.png')
    }
   },[mediaQuery])

  return (
    <div className='h-screen w-full flex justify-center items-center xs:flex-col snap-start '>
        <section className='w-[45%] h-[80%] xs:w-auto xs:h-auto'>
             <img src={imgUrl} alt="" className='h-full w-full'/>
        </section>

        <section className='w-[45%] h-[80%] xs:w-[90%]'>
            <div className='w-full h-[40%] ml-10 xs:ml-0'>
                <div className='h-[65%] w-full border-b-2 border-black'>
                    <h1 className='text-black text-3xl font-bold xs:hidden'> </h1>
                    <span className='w-full h-[80%] flex flex-col justify-center'>
                        <div className='h-2/5 justify-between flex xs:mb-3'>
                            <input type="text" className='bg-gray-300 w-[47%] lg:h-12 h-10 my-2 text-black rounded-none ' placeholder='' onChange={(e) => setName(e.target.value)} value={name}/>
                            <input type="text" className='bg-gray-300 w-[47%] lg:h-12 h-10 my-2 text-black rounded-none' placeholder='' onChange={(e) => setPhone(e.target.value)} value={phone}/>
                        </div>
                        <div className='h-2/5 justify-between flex'>
                            <input type="text" className='bg-gray-300 w-[47%] lg:h-12 h-10 my-2 text-black rounded-none' placeholder='E-mail' onChange={(e) => setEmail(e.target.value)} value={email}/>
                            <select className='bg-gray-300 w-[47%] lg:h-12 h-10 my-2 text-gray-500 rounded-none' onChange={(e) => setArea(e.target.value)} >
                                <option value=""> </option>
                                <option value=""> </option>
                                <option value=""> </option>
                                <option value=""> </option>
                                <option value=""> </option> 
                                <option value=""> </option> 
                            </select>
                        </div>
                    </span>
                </div>
                <div className='flex h-[35%] w-full items-center'>
                    <div className='w-1/2 h-2/5 flex items-center xs:w-3/5'>
                        <input type="checkbox" className='cursor-pointer mr-2 w-4 h-4 xs:mr-1' value={check} onChange={e => setCheck(!check)} checked={check}/>
                        <p className='font-bold xs:text-xs l'></p>
                    </div>
                    <button className='w-1/2 h-2/5 bg-stone-800 text-white xs:w-2/5' onClick={confirmBtn}></button>
                </div>
            </div>

            <div className='w-full h-[60%] ml-10 xs:ml-0 xs:h-[40%]'>
                <iframe style={{width: '100%', height: '100%'}} src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d14698.469217319973!2d120.27039486219566!3d22.927482200966228!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x346e73a601a91ea5%3A0x6364148ba1f16e05!2z5rKZ5bSZ6LuK56uZ!5e0!3m2!1szh-TW!2stw!4v1647585767200!5m2!1szh-TW!2stw" allowFullScreen="" loading="lazy"></iframe>
            </div>
        </section>
      
    </div>
  )
}

export default SendEmail