import React,{useEffect}from 'react'
import Aos from 'aos';
import 'aos/dist/aos.css';
function ForthComponent() {
    useEffect(()=>{
        Aos.init({duration: 1000})
    },[])
  return (
    <div className='h-screen w-full flex' style={{background: 'url("images/PC_1/shadow_3.png")',backgroundPosition: 'center',backgroundRepeat: 'no-repeat'}}>
        <img src="images/PC_1/1x/16.png" alt="" className='h-[80%] w-[45%] object-contain px-40 pt-40' data-aos='fade-right'/>
        <section className='flex justify-end w-[55%] h-full'>
            <div className='flex flex-col justify-center mr-2 w-[55%]'>
                <img src="images/PC_1/1x/12.png" alt="" className='mb-2 w-full' data-aos='fade-down' data-aos-offset="100"/>
                <img src="images/PC_1/1x/11.png" alt="" className='w-full' data-aos='fade-up'/>
            </div>
            <div className='h-full w-[45%]'>
                <img src="images/PC_1/1x/13.png" alt="" className='mb-2 h-[28%] w-full ' data-aos='fade-up'/>
                <img src="images/PC_1/1x/14.png" alt="" className='mb-2 h-[28%] w-full ' data-aos='fade-left'/>
                <img src="images/PC_1/1x/15.png" alt="" className=' h-[28%] w-full ' data-aos='fade-down'/>
            </div>
        </section>
    </div>
  )
}

export default ForthComponent