import React from 'react'
import Main from './desktop/Main'
import FirstComponent from './desktop/FirstComponent'
import SecondComponent from './desktop/SecondComponent'
import ThirdComponent from './desktop/ThirdComponent'
import ForthComponent from './desktop/ForthComponent'
import FifthComponent from './desktop/FifthComponent'
import {Link} from 'react-scroll'
function DesktopComponent() {
  return (
    <main className='overflow-x-hidden '>
        <Main />
        <FirstComponent/>
        <SecondComponent/>
        <ThirdComponent/>
        <ForthComponent/>
        <FifthComponent/>
        <Link className='w-14 h-14 fixed bottom-0 right-0 bg-yellow-500 text-white hover:bg-yellow-400 mb-10 flex justify-center items-center cursor-pointer'
            to="main"
            spy={true}
            smooth={true}
            offset={0}
            duration={500}
            
      >
          <span className='font-black'>TOP</span>
      </Link>
    </main>
  )
}

export default DesktopComponent