import React,{useEffect,useRef} from 'react'
import Aos from 'aos';
import 'aos/dist/aos.css';


function FirstComponent({first}) {
  useEffect(()=>{
    Aos.init({duration: 1000})
  },[])
  return (
    <div className='h-[80vh] flex flex-col justify-end items-center w-full snap-center'>
        <img src={'images/phone_1/1.png'} className='w-[70%] mb-12 ' id='firstcontainer' style={{animation: first ? 'slidedown 1s' : 'none',transform: 'translateY(-500px)',animationFillMode: first ? 'forwards' : 'none',opacity: '0',animationTimingFunction: first ? 'ease-in-out' : 'none' }}/>
        <img src={'images/phone_1/4.png'}  style={{animation: first ? 'slideup 1s' : 'none',transform: 'translateY(500px)',animationFillMode: first ? 'forwards' : 'none',opacity: '0',animationTimingFunction: first ? 'ease-in-out' : 'none' }}/>
    </div>
  )
}

export default FirstComponent