import React,{useEffect} from 'react'

function SecondComponent({second}) {
  return (
    <div className='h-[80vh] w-full flex flex-col justify-center items-center snap-center' >
        <div className='h-2/5 w-full'  >
            <img src='images/phone_1/3.png' className='h-full object-contain' id='secondcontainer' style={{animation: second ? 'slidedown 1s' : 'none',transform: 'translateY(-500px)',animationFillMode: second ? 'forwards' : 'none',opacity: '0',animationTimingFunction: second ? 'ease-in-out' : 'none' }}/>
        </div>
        <div className='h-3/5 w-full flex flex-col '>
            <section className='h-3/5 w-full px-6 pt-8 ' >
                <img src="images/phone_1/5.png" alt="" className='w-full h-full' style={{animation: second ? 'slideleft 1s' : 'none',transform: 'translateX(500px)',animationFillMode: second ? 'forwards' : 'none',opacity: '0',animationTimingFunction: second ? 'ease-in-out' : 'none' }}/>
            </section>
            <section className='h-2/5 flex w-full px-6 pt-2 pb-6'>
                <img src="images/phone_1/6.png" alt="" className='w-3/5 pr-2' style={{animation: second ? 'slideright 0.8s' : 'none',transform: 'translateX(-500px)',animationFillMode: second ? 'forwards' : 'none',opacity: '0',animationTimingFunction: second ? 'ease-in-out' : 'none' }}/>
                <img src="images/phone_1/7.png" alt="" className='w-2/5' style={{animation: second ? 'slideleft 1.2s' : 'none',transform: 'translateY(500px)',animationFillMode: second ? 'forwards' : 'none',opacity: '0',animationTimingFunction: second ? 'ease-in-out' : 'none' }}/>
            </section>

        </div>
    </div>
  )
}

export default SecondComponent