import React,{useEffect} from 'react'
import Aos from 'aos';
import 'aos/dist/aos.css';
function SecondComponent() {
    useEffect(()=>{
        Aos.init({duration: 1000})
    },[])
  return (
    <div className='relative w-full h-screen flex' style={{background: 'url("images/PC_1/shadow_2.png")',backgroundPosition: 'center',backgroundRepeat: 'no-repeat'}}>
        <img src="images/PC_1/1x/4.png" alt="" className='h-[85%] w-[55%]' data-aos='fade-right' />
        <section className='pl-10 w-[45%] h-full'>
            <div className='relative h-[60%] mb-4 w-full'>
                <img src="images/PC_1/1x/5.png" alt="" className='absolute left-0 bottom-0 ' data-aos='fade-down'/>
                <img src="images/PC_1/1x/6.png" alt="" className='absolute right-0 bottom-0 mr-28 '  data-aos='fade-left'  />
            </div>
            <div className='relative flex justify-end w-full pr-28 '>
                <img src="images/PC_1/1x/7.png" alt="" className='pr-4 h-[231px] w-[393px]' data-aos='fade-up'/>
                <img src="images/PC_1/1x/8.png" alt="" data-aos='fade-left'/>
            </div>
        </section>
    </div>
  )
}

export default SecondComponent