import React,{useEffect} from 'react'
import Aos from 'aos';
import 'aos/dist/aos.css';
function FirstComponent() {
    useEffect(()=>{
        Aos.init({duration: 1500})
    },[])
  return (
    <div className='h-screen w-full relative'>
        <img src="images/PC_1/1x/1.png" alt="" className='absolute top-[20%] left-0 ml-40 z-10' data-aos='fade-right'/>
        <img src="images/PC_1/1x/2.png" alt="" className='h-full w-full z-0' data-aos='fade-left'/>
    </div>
  )
}

export default FirstComponent