import React,{useEffect} from 'react'

function ThirdComponent({third}) {
  return (
    <div className='h-[80vh] w-full snap-center '> 
        <section className='h-[50%] p-12'>
            <img src='images/phone_1/9.png' className='' style={{animation: third ? 'slidedown 0.8s' : 'none',transform: 'translateY(-500px)',animationFillMode: third ? 'forwards' : 'none',opacity: '0',animationTimingFunction: third ? 'ease-in-out' : 'none' }}></img>
        </section>
        <section className='h-[50%] pt-4'> 
            <img src='images/phone_1/8.png' className='object-contain' style={{animation: third ? 'slideup 1s' : 'none',transform: 'translateY(500px)',animationFillMode: third ? 'forwards' : 'none',opacity: '0',animationTimingFunction: third ? 'ease-in-out' : 'none' }}></img>
        </section>
    </div>
  )
}

export default ThirdComponent